.button {
  width: 100%;
  height: 60px;
  border-radius: 10px;
  background-color: #67d89e;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #000;
  font-weight: 600;
  margin-top: 35px;
  margin-bottom: 11px;
}

.container {
  max-width: 358px;
  width: 100%;
  padding: 83px 17px 52px 17px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (max-width: 500px) {
  .button {
    margin-top: 24px;
  }
}
